import url from '@/request/url.js'
import {
	mapState
} from 'vuex'
export default {
	data() {
		return {
			countdowns: [],
		}
	},
	computed: {
		...mapState({
			userInfo: state => state.userInfo
		})
	},
	methods: {
		getAllCountDown(callback) {
			uni.request({
				url: url.getAllCountDown,
				method: "GET",
				data: {
					uid: this.userInfo.id
				},
				success: callback
			});
		}
	},
	onLoad() {
		this.getAllCountDown(resp => {
			console.log(resp.data);
			this.countdowns = resp.data;
		});
	}
}
